.volver{
    font-size: 4em;
}
form, .form {
  border: 2px solid #df2727;
  width: 80%;
  margin: 20px auto;
  border-radius: 18px;
  padding: 30px 20px;
  
}
.formulario{
  font-family: 'Ubuntu', sans-serif !important;
  font-size: 120%;
  margin-top: 140px;
}

.form{
  width: 60%;
}
form .label{
  color: rgb(75, 61, 61);
  font-size: 1em;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
form .form-group .form-control {
  background-color: #ffffff;
  color: #4d4d4d;
  font-size: 16px;
  width: 100%;
  height: 50px;
  border: 2px solid #df2727;
  border-radius: 18px;
  -webkit-box-shadow: none;
          box-shadow: none;
}
form .form-group .form-control:disabled {
  border-color: #4d4d4d;
  cursor: not-allowed;
}
.receta{
  border: 2px solid #4d4d4d;
  width: 80%;
  margin: 20px auto;
  border-radius: 10px;
  padding: 30px 20px;
}

.receta p{
  font-size: 1.3em;
}





